@import 'fonts';
@import './components/colors';

body {
  color: $lightGrey;
  background: #f5f5f5;
  font-family: 'TTNorms-Regular';
  font-weight: 300;
  line-height: 1.6;
  font-size: 16px;
  display: block;
}

.main {
  overflow: hidden;
}
img {
  pointer-events: none;
}
p, h1, h2, h3, h4, h5, span {
  letter-spacing: 1px;
  font-variant-ligatures: no-common-ligatures;
}

